/* Site styling */

*{
    font-family:system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    box-sizing: border-box;
}

body{
    margin: 0;
}

h1{
    padding-top: 4rem;
}

.nav-style{
    background-color: black;
    color: white;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 2rem;
    padding: 0 1rem;

    border-bottom: 2px solid white;

    font-size: 1.2rem;

    position: fixed;
    top: 0;
    width: 100%;

    z-index: 100;
}

.nav-style a{
    color: inherit;
}

.nav-style ul{
    padding: 1.6rem 0;
    margin: 0;
    list-style: none;
    display: flex;
    gap: 1rem;
}

/* Home page styles */

.homePage{
    background-color: black;
    padding-bottom: 8rem;
}

.homeSplash {
    width: 100vw;
    height: 100vh;
}

.homeSplash img{
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.homeSplash h1{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: white;

    font-size: 5rem;
    z-index: 2;

    margin: 0%;
    padding: 0%;
}

.homeSplash h2{
    position: absolute;
    top: 80%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: white;

    font-size: 3rem;
    z-index: 2;

    margin: 0%;
    padding: 0%;
}

.homeBox{
    margin: 3rem 15vw;
    width: 70vw;

    background-color: white;
    border-radius: 20px;

    display: flex;
    flex-direction: column;
    justify-content: center;
}

.homeBox img{
    width: 60vw;
    height: auto;
    padding-left: 12%;
    padding-top: 1rem;

    object-fit: contain;
}

.homeBox section{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 1rem 4rem;
}

/* writing page */

.linkBox{
    border: 3px solid black;
    width: 30vw;
    margin-left: 35vw;
    display: flex;
    align-items: center;
    flex-direction: row;
}

.linkBox a{
    padding: 2rem;
    font-size: 1.4rem;
}

.writingArticle {
    width: 80vw;
    margin: 5rem 10vw;

    border-left: 2px solid black;
    border-right: 2px solid black;

    padding-top: 2rem;
    padding-bottom: 2rem;
}

.writingArticle h2{
    padding-left: 3rem;
}

.writingArticle p{
    padding-left: 4rem;
}

/* Design */
.color-overlay {
    width: 100vw;
    height: 100vh;
    position: fixed;
    top: 0;
    left: 0;
    background-color: rgba(0, 0, 0, 0.500);
    z-index: 2;
}

.crabby {
    z-index: 3;
    position: fixed;
    width: 40vw;
    left: 30vw;
    top: 30vh;

    border: 15px solid black;
}

